import { model, Schema, models } from 'mongoose';

export default function getproductsModel() {
  const collectionName = 'products';
  if (models[collectionName]) {
    return model(collectionName);
  }
  const productsSchema = new Schema({
    productCode: {
      type: String,
      required: true,
    },
    itemCode: {
      type: String,
    },
    brandCode: {
      type: String,
    },
    productName: {
      type: String,
      required: true,
    },
    productSize: {
      type: String,
      required: true,
    },
    packType: {
      type: String,
      required: true,
    },
    basePrice: {
      type: Number,
    },
    exciseDuty: {
      type: Number,
    },
    segment: {
      type: String,
    },
  });

  return model(collectionName, productsSchema);
}
